export default {
  name: "loadingTest",
  data() {
    return {
      index: 0,
      showOption: false
    };
  },
  methods: {
    ClickLoading(type, title, theme, cb) {
      this.$loading.showLoading({ type, title, theme });
      setTimeout(() => {
        this.$loading.hideLoading(() => {
          if (cb) {
            this.$toast("我是加载完成后调用的(#^.^#)");
          }
        });
      }, 2000);
    }
  },
  mounted() {}
};



